@-webkit-keyframes rotation-gears {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(359deg);
                transform: rotate(359deg);
    }
}

@keyframes rotation-gears {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(359deg);
                transform: rotate(359deg);
    }
}

.rotate-gears {
    -webkit-filter: drop-shadow(0 0 0.5rem #000000);
            filter: drop-shadow(0 0 0.5rem #000000);
    -webkit-animation: rotation-gears 15s infinite linear;
            animation: rotation-gears 15s infinite linear;
}

@media (max-width: 576px) {
    .gear {
        max-width: 100%;
        margin-top: 0;
        max-height: 20rem;
    }

    .head {
        max-width: 100%;
        margin-top: 0;
        max-height: 20rem;
