.content {
      max-width: 1280px;
      overflow-x: hidden;
      margin: 0 auto;
      margin-bottom: 30px;
      font-family: "Avenir Next Condensed";
    }
    
    .card {
      height: 100%;
      display: flex;
      flex-direction: column;
      max-width: 24rem;
      position: relative;
      border: 1px solid #815AE0;
    }
    
   
    .card-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    
    .card-header span {
      color: #ffffbd;
    }
    .card-description{
      padding-left: 50px;
      padding-bottom: 20%;
    }
    .card-image {
      width: 100%;
      height: 14rem;
      max-width: 100%;
      object-fit: cover;
    }
    
    .card-button {
      display: inline-block;
      background-color: #815AE0;
      color: #fff;
      border: none;
      padding: 1.5em 1.6em;
      cursor: pointer;
      text-decoration: none;
      font-size: 15px;
      font-family: inherit;
      width: 100%;
      position: absolute;
      bottom: 0;
      transition: background-color 0.5s ease-out;
    }
    
    .text-info {
      padding: 1rem 1rem;
     
    }
